	module.exports = {"data":{"tweetsJson":{"from":"Kanban Dan Brown","date":"22 Sep 2014","tags":"#agile #lean","text":"Is Velocity a metric?","searchResults":[{"title":"Is velocity a useful metric? » Tools For Agile Blog » Blog Archive","href":"http://toolsforagile.com/blog/archives/224/is-velocity-a-useful-metric","description":"28 Jan 2010 ... In my talk at Agile Bengaluru, I asked “What is velocity? What does velocity \nmeasure? Is it useful?” What is velocity? Everyone was united on this – velocity is \nthe aggregate of story points (or any estimate unit you use) completed in the \nsprint. What does velocity measure? I got a bunch of answers for this."},{"title":"Velocity: the Killer of Agile Teams – Skeptical Agile","href":"https://skepticalagile.com/velocity-the-killer-of-agile-teams-1178409a72bd","description":"25 Feb 2016 ... Velocity in the agile world is a metric that can give the approximate “speed” of teams. After a couple of sprints in which you have a stable team and are working on a single product, if you average how many story points the team delivered, you get the average velocity of the team."},{"title":"Should Management Use Velocity as a Metric? - Agile Learning Labs","href":"http://www.agilelearninglabs.com/2013/08/should-management-use-velocity-as-a-metric/","description":"27 Aug 2013 ... Should Management Use Velocity as a Metric? Many well-intentioned managers have a fundamental misunderstanding about velocity. They think it is a measure of how hard the scrum team is working. ... Velocity is a measure of the rate at which the team is delivering stories."}],"index":295}},"pathContext":{"index":295}}

